@mixin font(
  $fontSize,
  $fontStyle,
  $fontWeight,
  $fontFamily,
  $lineHeight,
  $textTransorm,
  $color
) {
  font-family: $fontFamily;
  font-style: $fontStyle;
  font-weight: $fontWeight;
  font-size: $fontSize;
  line-height: $lineHeight;
  text-transform: $textTransorm;
  color: $color;
}

// style for panel navigations and mini-card-sliders
@media screen and (max-width: 820px) {

    .header__navbar {

        height: 50px;
        display: flex;
        flex-direction: row;
        justify-content: space-between;
        margin: 0 auto;

        .icon-navbar {
            margin:0;
            margin-top: 18px;
            margin-right: 12px;
            position: relative;
        }

        .menu-btn-mobile {
            width: 30px;
            height: 30px;
            position: absolute;
            top: 10px;
            left: 12px;
            z-index:5;
            overflow: hidden;
            display: block;
            &:active{
                scale: 1.05;
            }

            & > span {
                width: 30px;
                height: 2px;
                position: absolute;
                top: 50%;
                left: 50%;
                transform: translate(-50%, -50%);
                background-color: #222222;
                transition: all 0.5s;
                &:nth-of-type(2) {
                    top: calc(50% - 5px);
                }
                &:nth-of-type(3) {
                    top: calc(50% + 5px);
                }
            }
        }

        .navbar__nav {
            display: none;
        }

        .logo {
            margin:0;
            margin-top: 10px;
            position: relative;
            z-index: 8;
            left: 55px;
            @include font(24px, normal, 700, $font1, 29px, "", $blackActiveText);
        }

        .navbar__nav.active{
            display: flex;
            flex-direction: column;
            position: absolute;
            top: 40px;
            left: 0;
            min-width: 375px;
            width: 100%;
            max-width: auto;
            background-color: $white;
            margin: 5px auto;
            .nav__item-link {
                width: max-content;
                margin-bottom: 15px;
                margin-left: 12px;
                @include font(10px, normal, 700, $font1, 12px, uppercase, $greyText);
            }
        & a {
          text-decoration: none;
          &:hover {
            font-weight: 900;
            color: $blackActiveText;
            cursor: pointer;
          }
        }
    
        }


    }

    .miniCard_second, .miniCards {
        display: none;
    }
    
}